class Browser extends React.Component {
  constructor(props) {
    super(props);
    this.state  = {open: false, focusRed:false, focusBlue:false, pill:"", step: 0, fail:""};
    this.takePill = this.takePill.bind(this);
    this.handleClose = this.handleClose.bind(this);
  }
  takePill(pill){
    this.setState({open:true,pill:pill});
  }
  handleClose(){
    this.setState({open:false});
  }

  render() {
    return (
    <React.Fragment>
    <Container style={{position: "absolute", top:"2%"}}>
    <a href="/"><img src={logo1} alt="logo-title" style={{width:'25%'}} /></a>
    </Container>
      <div style={{position: "absolute", top:"8%", left: "50%"}}>
        <Container style={{position: "relative", left: "-50%", alginItems: "center", textAlign: "center", color:"#ffffff"}}>       
          <Stack spacing={0}>
           <Typography variant="h5" component="h2"> Game One </Typography>
           <Typography variant="h3" style={{fontFamily: "Franklin Gothic"}}> Red Pills or Blue Pills </Typography>
           <Typography variant="h6" > <i>Take one and begin your quest.</i> </Typography>
          </Stack>
        </Container>
      </div>
     <Box xs={{ position: 'relative'}}>
      <Grid container spacing={0}>
          <Grid item xs={6}>
            <Box sx={{ bgcolor: this.state.focusRed?'#d41f1f':'#6e1010', height: '100vh' }} 
                      onMouseOver={(e) => this.setState({focusRed:true})} 
                      onMouseOut={(e) => this.setState({focusRed:false})}>
               <Container style={{ textAlign:"center", position: 'relative', top:"35%"}}>
                 <Stack spacing={8}>
                     <Container style={{ textAlign:"center"}} >
                        <img src={tsIcon} 
                        style={{ overflow: "hidden", width:'35%'}}
                        alt= "" />
                     </Container>
                     <Container style={{ textAlign:"center"}}>
                        <RedButton variant="outlined" onClick={()=> this.setState({open:true,pill:"red"})}>Take Red</RedButton>
                     </Container> 
                  </Stack>
               </Container>
            </Box>
          </Grid>
      
        <Grid item xs={6}>
          <Box sx={{ bgcolor: this.state.focusBlue?'#31a0bf':'#195262', height: '100vh' }}
                onMouseOver={(e) => this.setState({focusBlue:true})} 
                onMouseOut={(e) => this.setState({focusBlue:false})} >
             <Container style={{ textAlign:"center", position: 'relative', top:"35%"}}>
                <Stack spacing={8}>
                   <Container style={{ textAlign:"center"}} >
                      <img src={tsIcon} 
                      style={{ overflow: "hidden", width:'35%' }}
                      alt= "" />
                   </Container>
                   <Container style={{ textAlign:"center"}}>
                      <BlueButton variant="outlined" onClick={()=> this.setState({open:true,pill:"blue"})}>Take Blue</BlueButton>
                   </Container> 
                </Stack>
             </Container>
          </Box>
        </Grid>
      </Grid>
    </Box>
      <Web3ReactProvider getLibrary={(provider, connector)=>new Web3Provider(provider)}>
        <Wallet open={this.state.open} close = {() => this.setState({open:false})} pill={this.state.pill} onFail={(fail)=>this.setState({fail:fail})}/>
      </Web3ReactProvider>
      <Snackbar open={this.state.fail!==""} autoHideDuration={6000} onClose={()=>this.setState({fail:""})}>
        <Alert onClose={()=>this.setState({fail:""})} severity="warning" sx={{ width: '100%' }}>
          {this.state.fail}
        </Alert>
      </Snackbar>
    </React.Fragment>
  );
  }
}

