const server_url = 'https://nftest.io/app'

class ChooseNFT extends React.Component {
  constructor(props) {
    super(props);
    this.state  = {nfts:[], choice: "", loadedAccount:"", isLoading:false, success:false, fail:""};
    this.signUp = this.signUp.bind(this);
    this.getNFT = this.getNFT.bind(this);
    this.updateNFT = this.updateNFT.bind(this);
    this.setLoading = this.setLoading.bind(this);
  }
  /*componentDidMount(){
    this.setState({account:this.props.account})
    if (this.props.account) {
      this.setState({nfts:[
        {"owner":"0xb88f61e6fbda83fbfffabe364112137480398018","id":67818212,"token_id": "5521","contract":"0x9a534628b4062e123ce7ee2222ec20b86e16ca8f","name": "Meka #5521",
        "avatar": "https://lh3.googleusercontent.com/a3bCByilokHGDpN32wyZAEEB1V0qo1g73wstHHclAA_AhH3m7d2vyQ-8gciokwSMN5iZyBDgcbAlVZGz3Qr2qA1Kbx1K3_GKqsjzqQ=s128",}]})
    }
  } */
  componentDidMount(){
      this.getNFT(this.props.account);
    }
  componentDidUpdate(prevProps) {
      this.getNFT(this.props.account);
    }
  updateNFT(loadedAccount, newNFTs){
    this.setState({loadedAccount: this.props.account, isLoading: false, nfts:newNFTs});
  }
  setLoading(flag){
    this.setState({isLoading: flag?true:false});
  }
  getNFT(account, callback=this.updateNFT){
    const props_account = this.props.account;
    if (account !== this.state.loadedAccount && account !== "" && (!this.state.isLoading)){
      this.setLoading(true);
      console.log('loading '+this.props.account);
      fetch("https://api.opensea.io/api/v1/assets?owner="+props_account+"&order_by=sale_price&order_direction=desc&offset=0&limit=30")
        .then(res => res.json())
        .then(function(res) {
          var newNFTs=[];
          if (res.assets){
              for (var i=0; i<res.assets.length; i++){
                newNFTs.push({
                  "id":res.assets[i].id,
                  "token_id": res.assets[i].token_id,
                  "contract": res.assets[i].asset_contract.address,
                  "name": res.assets[i].name,
                  "avatar": res.assets[i].image_thumbnail_url,
                  "owner": res.assets[i].owner.address
                });
              }
              fetch(server_url+"/getListedNFTbyAddress?address="+props_account).then(res => res.json())
                .then(function(res) {
                  var neuNFTs = [];
                  var tag='';
                  var loadedNFT={};
                  if (res.success && res.data){
                    for (var i=0; i<res.data.length; i++){
                      tag = res.data[i].contract + res.data[i].tokenId;
                      loadedNFT[tag] = res.data[i].id;
                    }
                  }
                  console.log(loadedNFT);
                  for (var i=0; i<newNFTs.length; i++){
                    if (! ((newNFTs[i].contract + newNFTs[i].token_id) in  loadedNFT)){
                      neuNFTs.push(newNFTs[i]);
                    }
                  }
                  callback(account, neuNFTs);
                })
          }
        })
    }
  }
  signUp(){
    const success = () =>{this.setState({success:true})};
    const fail = (msg) =>{this.props.onFail(msg)};
    const msg = 'Take '+ this.props.pill +': '+this.state.choice
    this.props.library
      .getSigner(this.props.account)
      .signMessage(msg)
      .then((signature) => {
        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ data:{
              msg: msg, sign: signature
            } })
        };
        fetch(server_url+'/listNFT', requestOptions)
          .then(res => res.json())
          .then(function(res){
            if(res.success){
              success();
            }
            else{
              fail(res.msg);
            }
          })
        
      })
      .catch((error) => {
        this.props.onFail((error && error.message ? `\n\n${error.message}` : ''))
      })
  }
  render(){return (
    this.state.success?
    <Container style={{textAlign:"center", display:"flex",justifyContent: 'center', alginItems:'center'}}>
        <Stack spacing={4} style={{ display:"flex", marginLeft:'auto', marginRight:'auto'}}>
          <Typography variant="h5">
            <b>Success</b>
          </Typography>
            <Typography variant="body1">
            You have taken the {this.state.pill} pill.<br/> Something is growing inside you, for sure...
          </Typography>
              
          <Button variant="contained" onClick={()=>this.props.success()} style={{width:'70%', marginLeft:'auto', marginRight:'auto'}}> 
          Let's wait </Button>
             
             
         </Stack>
      </Container> :
      <Container style={{textAlign:"center", display:"flex",justifyContent: 'center'}} align="center">
        <Stack spacing={4} style={{ display:"flex", marginLeft:'auto', marginRight:'auto'}} align="center">
          <Typography variant="h5">
            <b>Choose NFT</b>
          </Typography>
           {this.state.isLoading?
             <Container align="center">
            <CircularProgress /></Container>:
            <Select
                value={this.state.choice}
                onChange={ (event) => this.setState({choice:event.target.value})}
                style={{width:'80%', marginLeft:'auto', marginRight:'auto'}}
              >
                {this.state.nfts.map((nft)=>
                  <MenuItem value={nft.name+'@'+nft.contract+'#'+nft.token_id} key={nft.id}>
                    
                  <Grid container spacing={1} style={{display:"flex",justifyContent: 'center'}}>
                      <Grid item xs={4} style={{display:"flex",justifyContent: 'center'}}>
                       <img style={{borderRadius:"50%", width:"23px", height:"23px"}} src={nft.avatar} crossOrigin="anonymous"/>
                      </Grid>
                      <Grid item xs={8} style={{display:"flex",justifyContent: 'center'}}>
                        <Typography>{nft.name}</Typography>
                      </Grid>
                    </Grid>
                  </MenuItem>
                  )}
              </Select>
            }
              {this.state.choice!==""?
                <Button variant="contained" onClick={this.signUp} style={{width:'70%', marginLeft:'auto', marginRight:'auto'}}> Choose </Button>:
                <Button variant="contained" disabled style={{width:'70%', marginLeft:'auto', marginRight:'auto'}}> Choose </Button>
              }
             
             <Typography variant='caption'>
                <i>Only NFTs distributed on Ethereum and listed on Opensea is supported.</i>
              </Typography>
         </Stack>
      </Container> 
      
    )}
}

function Wallet(props) {
  const ModalStyle = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: props.isMobile? 200:400,
    bgcolor: 'background.paper',
    borderRadius: '10px',
    boxShadow: 24,
    display:"flex",
    justifyContent: 'center',
    p: 4,
  };
  const web3React = useWeb3React();
  const injected = new InjectedConnector({
    supportedChainIds: [1, 3, 4, 5, 42],
  });
  const steps = [
    'Connect Wallet',
    'Choose NFT',
  ];
  function success(){
    web3React.deactivate();
    props.close();
  }
  async function connectInjected() {
    try {
      await web3React.activate(injected)
    } catch (ex) {
      alert(ex);
    }
  }
  return (
    <Modal
        open={props.open}
        onClose={(event, reason) => {if (reason !== 'backdropClick') {props.close()}}}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >

        <Box sx={ModalStyle}>
          <IconButton
            aria-label="close"
            onClick={()=>props.close()}
            sx={{
              position: 'absolute',
              right: 8,
              top: 8,
              color: (theme) => theme.palette.grey[500],
            }}
          >
            <CloseIcon />
          </IconButton>

         <Container style={{ display:"flex",justifyContent: 'center'}}>
          {!web3React.account && (
            <Container style={{ textAlign:"center"}}>
              <Stack spacing={4}>
                <Typography variant="h5">
                  <b>Connect Wallet</b>
                </Typography>
                  <Container style={{ textAlign:"center"}}>
                    <Button variant="contained" style={{width: "100%"}} onClick={connectInjected}> Metamask </Button>
                  </Container> 
               </Stack>
            </Container> 
           )}
          {
            web3React.account && (

              <ChooseNFT account={web3React.account} library={web3React.library} pill={props.pill} onFail={props.onFail}
                success={success}/>
              )
          }
          </Container>
        </Box>
      </Modal>
  );  
}

class Main extends React.Component {
  constructor() {
    super();
    this.state = {
      width: window.innerWidth,
    };
  }

  componentWillMount() {
    window.addEventListener('resize', this.handleWindowSizeChange);
  }

  // make sure to remove the listener
  // when the component is not mounted anymore
  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }

  handleWindowSizeChange = () => {
    this.setState({ width: window.innerWidth });
  };

  render() {
    const { width } = this.state;
    const isMobile = width <= 500;

    if (isMobile) {
      return (
        <Mobile/>
      );
    } else {
      return (
        <Browser/>
      );
    }
  }
}

export default () => (
  <Main />
)

