import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Game1 from './Game1';
import Home from './Home';

ReactDOM.render(
  <React.StrictMode>
    <Router>
        <Switch>
          <Route path="/pill">
            <Game1 />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>
    </Router>
  </React.StrictMode>,
  document.getElementById('root')
);
