import React from 'react';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import { styled } from '@mui/material/styles';
import { makeStyles } from '@mui/styles';
import Typography from '@mui/material/Typography';

import mainPage from './assets/mainPage.png';
import game1Button from './assets/game1button.png';
import joinButton from './assets/Join.png';
import ruleButton from './assets/Rule.png';
import twitterButton from './assets/Twitter.png';
import telegramButton from './assets/Telegram.png';
import discordButton from './assets/Discord.png';

const GameCaption = styled(Typography)({
  color: "black",
  position: "absolute",
  top: "855px",
  left: "64px",
  fontSize: "14px",
  fontFamily: "Calibri",
  width:"270px",
});

function GameButton(props) {
    const useStyles = makeStyles({
          root: {
            border: "none",
            position: "absolute",
            top: props.top,
            left: props.left,
            padding: "0",
          },
        });
     return (
        <div className={useStyles().root} > 
            <a href={props.href}>
                <img src={props.img} style={{cursor: "pointer"}}/>
            </a>
        </div>
     )

}


var delta = Math.abs(1635654600000 - Date.now()) / 1000;
var days = Math.floor(delta / 86400);
delta -= days * 86400;
var hours = Math.floor(delta / 3600) % 24;

class Home extends React.Component {
    render () {
        return <React.Fragment>
        <Box>
            <Container align="center" style={{padding:'0', }}>
                <Box sx={{display : 'inline-block' ,position: "relative"}}>
                    <img src={mainPage}/>
                    <GameCaption> OPENING FOR SINGING UP: {days} DAYS {hours} HOURS </GameCaption>
                    <GameButton top="813px" left="126px" img={game1Button} href="/pill"/>
                    <GameButton top="539px" left="178px" img={joinButton} href="/pill"/>
                    <GameButton top="539px" left="498px" img={ruleButton} href="#rule"/>
                    <GameButton top="1496px" left="257px" img={twitterButton} href="#"/>
                    <GameButton top="1496px" left="475px" img={telegramButton} href="#"/>
                    <GameButton top="1496px" left="560px" img={discordButton} href="#"/>
                    <div id="rule" style={{position: "absolute", top:"634px"}}/>
                </Box>
            </Container>
        </Box>
        </React.Fragment>
    }
}

export default () => (
  <Home />
)